

@import url('https://fonts.googleapis.com/css2?family=Advent+Pro:ital,wght@0,100..900;1,100..900&display=swap');
.hero{
    background-color: rgb(244, 234, 215);
    overflow: hidden;
    margin-top: 50px;
  }
  .heading h1{
    color:rgb(48, 45, 45);
    font-size: 40px;
    text-align: center;
    margin-top: 35px;
    font-family: sans-serif;
  }
  .container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    margin:30px auto;
  }
  .hero-content{
    flex: 1;
    width: 600px;
    margin: 0px 25px;
    animation: fadeInUp 2s ease;
  }
  .hero-content h2{
    font-size: 38px;
    margin-bottom: 20px;
    color: #333;
    font-family: sans-serif;
    font-weight: 700;
  }
  .hero-content p{
    font-size: 20px;
   line-height: 1.5;
   margin-bottom: 40px;
   color: #666;
   font-family: sans-serif;
  }
  .hero-content button{
    display: inline-block;
    background-color: rgb(48, 45, 45);
    color: #fff;
    padding: 12px 24px;
    border-radius: 5px;
    font-size: 20px;
    border: none;
    cursor: pointer;
    transition: 0.3s ease;
  }
  .hero-content button:hover{
    background-color: #ff6347;
    transform: scale(1.1);
  }
  
  .hero-image{
    flex: 1;
    width: 600px;
    margin: auto;
    animation: fadeInRight 2s ease;
  }
  .hero-image img{
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  @media screen and (max-width:768px) {
    .heading h1{
      font-size: 45px;
      margin-top: 30px;
    }
    .hero{
      margin: 0px;
    }
    .container{
      width: 100%;
      flex-direction: column;
      margin: 0px;
      padding: 0px 40px;
    }
    .hero-content{
      width: 100%;
      margin: 35px 0px;
    }
    .hero-content h2{
      font-size: 30px;
    }
    .hero-content p{
      font-size: 18px;
      margin-bottom: 20px;
    }
    .hero-content button{
      font-size: 16px;
      padding: 8px 16px;
    }
    .hero-image{
      width: 100%;
    }
  }
  
  @keyframes fadeInUp {
    0% {
      opacity: 0;
      transform: translateY(50px);
    }
    100% {
      opacity: 1; /* Change opacity to 1 for element to fade in */
      transform: translateY(0px);
    }
  }
  
  @keyframes fadeInRight {
    0% {
      opacity: 0;
      transform: translateX(-50px);
    }
    100% {
      opacity: 1; /* Change opacity to 1 for element to fade in */
      transform: translateX(0px);
    }
  }
  